<script>
export default {
  mounted() {
    let data = JSON.parse(window.localStorage.getItem("machineID"));
    this.meter_no = data
    this.get_energy_all();
  },
  data() {
    return {
      year: "",
      items: [],
      select_year: "",
      meter_no: ""
    };
  },
  computed: {
    years() {
      const year = new Date().getFullYear();

      return Array.from(
        { length: year - 2019 },
        (value, index) => 2020 + index
      );
    },
  },
  methods: {
    get_energy_all() {
      this.year = new Date().getFullYear();
      this.axios({
        method: "GET",
        url:
          this.$store.state.url_axios +
          "api/v1/energy/" +
          this.meter_no +
          "?year=" +
          this.year,
        headers: { Authorization: "Bearer " + localStorage.token },
      }).then((response) => {
        // console.log(response.data.result);
        this.items = response.data.result;
      });
    },
    get_energy_is_year(value) {
      console.log(value);
      this.select_year = value;

      if (this.select_year != "") {
        // console.log('gggg');
        //   this.year = new Date().getFullYear();
        this.axios({
          method: "GET",
          url:
            this.$store.state.url_axios +
            "api/v1/energy/mm_600194433DCF?year=" +
            this.select_year,
          headers: { Authorization: "Bearer " + localStorage.token },
        }).then((response) => {
          // console.log(response.data.result);
          this.items = response.data.result;
          // this.$refs.form.reset();
          // this.value = ""
        });
      }
    },
  },
};
</script>

